import React from "react";
import AppBar from "material-ui/AppBar";

const NavBar = () => (
  <AppBar
    style={{
      background: "linear-gradient(90deg, #00D1B1 2.5%, #0074D7 97.5%)",
      textAlign: "center",
    }}
    title="Find your favourite images"
  />
);

export default NavBar;



